<template>
  <div>
    <div class="lf-center" :style="{height: htmlProportion * 260 + 'px'}">
      <span class="iconfont lf-icon-close text-lllg text-red"></span>
    </div>

    <div class="lf-text">

      <div class="lf-between lf-border-line" :style="{height: htmlProportion * 80 + 'px'}">
        <span class="text-grey">错误代码：</span>
        <span class="text-sm">{{errCode}}</span>
      </div>

      <div class="lf-between lf-border-line" :style="{height: htmlProportion * 80 + 'px'}">
        <span class="text-grey">错误信息：</span>
        <span class="text-sm">{{errMsg}}</span>
      </div>
    </div>

  </div>
</template>

<script>
  export default{
    data() {
      return{
        htmlProportion: '',		//屏幕长宽比例
        htmlWidth: '',		//屏幕宽
        htmlheight: '',		//屏幕高

        errCode: "",
        errMsg: "",
      }
    },
    created() {
      this.htmlWidth = document.documentElement.clientWidth || document.body.clientWidth;
      this.htmlheight = document.documentElement.clientHeight || document.body.clientHeight;
      this.htmlProportion = this.htmlWidth / 750 ;
      this.errCode = this.$route.query.errCode;    //错误code
      this.errMsg = this.$route.query.errMsg;    //错误信息
    }, methods:{
    }
  }
</script>

<style>
  body{
    background-color: #FFF;
  }
  .text-sm{	/* 14 */
    font-size: 0.875rem;
  }
  .text-lg{	/* 24 */
    font-size: 1.5rem;
  }
  .text-llg{	/* 36 */
    font-size: 2.25rem;
  }
  .text-lllg{	/* 64 */
    font-size: 4rem;
  }
  .text-weight{
    font-weight: 800;
  }
  .text-ff4500{	/* 橙红 */
    color: #ff4500;
  }
  .text-2dc26f{	/* 绿 */
    color: #2dc26f;
  }
  .text-grey{		/* 草灰 */
    color: #a9a9a9;
  }
  .text-red{		/* 红色 */
    color: #E60000;
  }
  .text-color-blue{
    color: #4BA8FF;
  }
  .margin-right-10{
    margin-right: 0.625rem;
  }
  .lf-nav{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    /* height: 80px; */
    background-color: #F5F2F0;
    /* border-bottom: 0.0625rem solid #bdbdc6; */
  }
  .lf-nav-mat{
    width: 100%;
    /* height: 40px; */
  }
  .lf-nav-text{
    width: 100%;
    /* height: 80px; */
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .lf-nav-text > div{
    display: flex;
    align-items: center;
  }
  .lf-nav-text span{
    margin: 0 0.625rem;
  }
  .lf-center{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .lf-text{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
  }
  .lf-between{
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .lf-border-line{
    border-bottom: 0.0625rem solid #bdbdc6;
  }
  .adver-ban{
    margin-top: 1.2rem;
    position: relative;
    height: 0;
    width: 100%;
    padding-top: 80%;
  }
  .adver-ban-body{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  .adver-ban-carousel{
    width: 100% !important;
    height: 100% !important;
  }
  .adver-ban-carousel img{
    width: 100%;
    height: 100%;
  }
  .adver-ban-carousel .mu-carousel-indicator-icon {
    width: 7px;
    height: 7px;
  }
  .adver-ban-carousel .mu-carousel-indicator-button{
    width: 7px;
    height: 7px;
    margin: 0 5px;
  }
  .adver-ban-carousel .mu-carousel-indicator-icon{
    opacity: 1;
    background-color: #fff;
  }
  .adver-ban-carousel .mu-carousel-indicator-button__active .mu-carousel-indicator-icon {
    background-color: #4BA8FF;
  }
</style>



